import React, { useState, useEffect } from "react";
import {
  Typography,
  Table,
  Divider,
  Icon,
  Input,
  Row
} from "antd";
import axios from "axios";
import { err503, err401, errGeneral } from "../../services/messages";
const { Paragraph } = Typography;

export default function Usuarios() {
  const [usuario, setUsuario] = useState([]);
  const [usuarioFiltered, setUsuarioFiltered] = useState([]);
  const [state, setState] = useState(true);


  useEffect(() => {
    axios
      .get("/usuarios")
      .then(res => {

        let filteredWithKey = res.data.map((item, key) => {
          let returnData = item;
          returnData.key = key;
          return returnData;
        });
        setUsuarioFiltered(filteredWithKey);
        setUsuario(res.data);
        setState(true);
        
      })
      .catch(err => {
        

        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  const filterUsuario = value => {
    setUsuarioFiltered(
      usuario.filter(
        r =>
          (r.nome && r.nome.toLowerCase().includes(value.toLowerCase()))
      )
    );
  };

  const columns = [
    { title: "Nome do Cliente", dataIndex: "nome", key: "nome" },
    { title: "CPF", dataIndex: "cpf", key: "cpf" },
    { title: "Celular", dataIndex: "telefoneCel", key: "telefoneCel" },
    { title: "Telefone Fixo", dataIndex: "telefoneFixo", key: "telefoneFixo" },
    { title: "E-mail", dataIndex: "email", key: "email" }
  ];

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Clientes{" "}
      </Paragraph>

      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="user" />
      </Divider>


      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>

        <Input.Search
          placeholder="Procurar por nome"
          onChange={e => filterUsuario(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />
      </Row>
      <Table
        style={{ marginTop: 20 }}
        columns={columns}
        dataSource={usuarioFiltered}
      />
    </>
  );
}
