// layout.js
import React, { Component } from "react";
import { Layout, BackTop, Breadcrumb } from "antd";

import NavBar from "./navbar/navbar.js";
import MenuContent from "./menu/menu";
import Footer from "./footer/footer";

import { getNome } from "../services/auth";
import "./home.css";

const { Content } = Layout;

export default class MainLayout extends Component {
  constructor() {
    super();

    this.state = {};
  }

  render() {
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <BackTop />
        <MenuContent page={this.props.page} toggleChat={this.toggleChat} />
        <Layout>
          <NavBar />
          <Content style={{ margin: "16px", display: "flex" }}>
            <div className="breadcrumb" style={{ flex: "1" }}>
              <Breadcrumb style={{ margin: "16px 0", paddingLeft: "20px" }}>
                {/* <Breadcrumb.Item></Breadcrumb.Item> */}
                <Breadcrumb.Item>Acessado por {getNome()}</Breadcrumb.Item>
              </Breadcrumb>
              <div
                style={{ padding: 24, background: "#F5F5F5", minHeight: "100%" }}
              >
                {this.props.children}
              </div>
            </div>
          </Content>
          <Footer />
        </Layout>
      </Layout>
    );
  }
}
