import React, { useState, useEffect } from "react";
import ReactEcharts from "echarts-for-react";
import echarts from 'echarts';
import { Typography, Divider, Icon } from "antd";
import axios from "axios";
import { err503, err401, errGeneral } from "../../services/messages";

const { Paragraph } = Typography;

export default function Estatisticas() {
  const [estatisticas, setEstatisticas] = useState([]);
  const [state, setState] = useState(false);

  useEffect(() => {
    axios
      .get("/estatisticas")
      .then(({ data }) => {
        setEstatisticas(data[0]);
      })
      .catch(err => {
        setState(false);
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  echarts.registerTheme('my_theme', {
    backgroundColor: '#E5E0DA'
  });


  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Estatísticas{" "}
      </Paragraph>

      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="bar-chart" />
      </Divider>

      <ReactEcharts
        theme='my_theme'
        option={{
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            x: "left",

            data: [
              "Cadastro no Aplicativo",
              "Consulta de Bicos",
              "Consulta de Família de Produtos",
              "Consulta de Produtos",
              "Cálculo de Vazão",
              "FAQ",
              "Novo Chamado",
              "Solicitar Orçamento"
            ]
          },
          series: [
            {
              name: "Acessos pelo aplicativo",
              type: "pie",
              radius: ["73%", "90%"],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: "center"
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: "20",
                    fontWeight: "bold",
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [
                { value: estatisticas.CADASTRO ? estatisticas.CADASTRO : 0, name: "Cadastro no Aplicativo" },
                { value: estatisticas.BICOS ? estatisticas.BICOS : 0, name: "Consulta de Bicos" },
                { value: estatisticas.FAMILIAS ? estatisticas.FAMILIAS : 0, name: "Consulta de Família de Produtos" },
                { value: estatisticas.PRODUTO ? estatisticas.PRODUTO : 0, name: "Consulta de Produtos" },
                { value: estatisticas.CALC_VAZAO ? estatisticas.CALC_VAZAO : 0, name: "Cálculo de Vazão" },
                { value: estatisticas.FAQ ? estatisticas.FAQ : 0, name: "FAQ" },
                { value: estatisticas.NOVO_CHAMADO ? estatisticas.NOVO_CHAMADO : 0, name: "Novo Chamado" },
                { value: estatisticas.SOLICITAR_ORCAMENTO ? estatisticas.SOLICITAR_ORCAMENTO : 0, name: "Solicitar Orçamento" }
              ]
            }
          ]
        }}
      />
    </>
  );
}
