import React, { useState, useEffect } from "react";
import {
  Typography,
  Table,
  Divider,
  Icon,
  Tag,
  Modal,
  List,
  Row,
  Input,
  Select,
  Checkbox,
  Popconfirm,
} from "antd";
import axios from "axios";
import { err503, err401, errGeneral, sucess, waitService } from "../../services/messages";
const { Paragraph } = Typography;
const { Option } = Select;

export default function Usuarios() {
  const [orcamento, setOrcamento] = useState([]);
  const [orcamentoFiltered, setOrcamentoFiltered] = useState([]);
  const [state, setState] = useState(true);
  const [modal, setModal] = useState(false);
  const [habilityFilter, setHabilityFilter] = useState(true);

  useEffect(() => {
    axios
      .get("/orcamentos/")
      .then(res => {
        let filteredWithKey = res.data.map((item, key) => {
          let returnData = item;
          returnData.key = key;
          return returnData;
        });
        setOrcamentoFiltered(filteredWithKey);
        setOrcamento(res.data);
        setState(true);
        
      })
      .catch(err => {
        

        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  const filterOrcamento = value => {
    setOrcamentoFiltered(
      orcamento.filter(r =>
          (r.nome && r.nome.toLowerCase().includes(value.toLowerCase())) ||
          (r.email && r.email.toLowerCase().includes(value.toLowerCase())) ||
          (r.mensagem && r.mensagem.toLowerCase().includes(value.toLowerCase())) 
          
      )
    );
  };

  const filterStatus = value => {    
    setOrcamentoFiltered(
      orcamento.filter(
        r =>
          (r.status === value)
      )
    );
  };



  const alterStatus = e => {
    e.status = !e.status;
    waitService();
    axios.put("/orcamentos/", { id: e._id, status: e.status }).then(() => {
      setState(false);
      hideModal();
      sucess();
    }).catch(err => {
      if (err.response && err.response.status === 503) err503();
      else if (err.response && err.response.status === 401) err401();
      else errGeneral();
    })
  };

  const hideModal = () => {
    setModal(false);
  };

  const columns = [
    {
      title: "Situação",
      dataIndex: "status",
      key: "status",
      render: (status, record) => {
        let color = "";
        let tag = "";

        if (status === false) {
          color = "red";
          tag = "Em aberto";
        } else if (status === true) {
          color = "green";
          tag = "Respondido";
        }
        return (
          <span>
            <Popconfirm title="Deseja altererar a situação?"
              onConfirm={() => alterStatus(record)}
              okText="Sim"
              cancelText="Não">
              <Tag color={color}>
                {tag.toUpperCase()}
              </Tag>
            </Popconfirm>
          </span>
        );
      }
    },
    { title: "Nome do Cliente", dataIndex: "nome", key: "nome" },
    { title: "E-mail", dataIndex: "email", key: "email" },
    { title: "Mensagem", dataIndex: "mensagem", key: "mensagem" }
  ];

  return (
    <>
      <Modal visible={modal} footer={null} onCancel={() => hideModal}>
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Confirmar encaminhamento de orçamento{" "}
        </Paragraph>

      </Modal>

      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Orçamento{" "}
      </Paragraph>

      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="folder-open" />
      </Divider>


      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>

        <Input.Search
          placeholder="Procurar por nome"
          onChange={e => filterOrcamento(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />
      </Row>

      <Row >
        <Checkbox
          onChange={({ target }) => {
            console.log(target.checked);
            setHabilityFilter(!target.checked);
            setState(false);
          }}
        >
          Filtrar :
        </Checkbox>


        <Select
          showSearch
          style={{ width: 600 }}
          placeholder="Filtrar por situação"
          optionFilterProp="children"
          autoClearSearchValue="true"
          disabled={habilityFilter}
          onSelect={filterStatus}

          filterOption={(input, option) =>
            option.props.children
              .toLowerCase()
              .indexOf(input.toLowerCase()) >= 0
          }
        >
          <Option value={true} key={0}>
              Respondido
            </Option>
            <Option value={false} key={1}>
              Aberto
            </Option>
        </Select>
      </Row>

      <Table
        style={{ marginTop: 20 }}
        columns={columns}
        dataSource={orcamentoFiltered}
        expandedRowRender={record => (
          <div style={{ margin: 0 }}>
            {record.produto.map(p => (
              <List.Item key={p._id}>Produto: {p.id?p.id.nome:""} | Quantidade: {p.quantidade}</List.Item>
            ))}
          </div>
        )}
      ></Table>
    </>
  );
}
