import React, {useState} from 'react';
import { Link } from 'react-router-dom';


import { Layout, 
  Menu, 
  Dropdown, 
  Icon, 
  Button, 
  Typography,
  Modal,
  Divider,
  Form,
  Input  
} from 'antd';

import axios from "axios";

import {
  err503,
  err401,
  errGeneral,
  sucess,
} from "../../services/messages";

import './navbar.css';
import { getID } from "../../services/auth";
import { logout } from '../../services/auth';

const { Header } = Layout;
const { Paragraph } = Typography;

//hooks ou state


// modal Editar
const EditarPerfilForm = ({ visivel, hideModal, usuario, setUsuario, editarPerfil, ...props }) => {
  const { getFieldDecorator, getFieldValue, validateFields } = props.form;
  
  //Validar Senha


  const validarSenha = (rule, value, callback) => {
    if (value && getFieldValue('confirmarSenha')) {
      validateFields(['confirmarSenha'], { force: true });
    }
    callback();   
     
  }

  const compararPrimeiraSenha = (rule, value, callback) => {
    if(value && value !== getFieldValue('senha')){
      callback('A senha digitada não corresponde!')
    } 
      callback();
  }
  
  return (
    <Modal
      visible={visivel}
      footer={null}
      onCancel={() => hideModal()}
      centered
    >
      <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
      >
        {" "}
        Editar Perfil{" "}
      </Paragraph>
      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="edit" />
      </Divider>
      
      {/*Formulario de edição do perfil do usuario*/}

      <Form style={{ marginTop: 10 }}>
        <Form.Item label="Nome">
          <Input
            name="nome"
            placeholder="Nome"
            prefix={<Icon type="smile" style={{ color: "rgba(0,0,0,.25)" }} />}
            value={usuario.nome}
            onChange={e => {
              const novoNome = { nome: e.target.value, login: usuario.login, senha: usuario.senha };
              setUsuario(novoNome);
              }
            }
            required
          />
        </Form.Item>

        <Form.Item label="Login">
          <Input
            placeholder="Login"
            name="login"
            prefix={
              <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
            }
            value={usuario.login}
            onChange={e => {
              const novoLogin = { nome: usuario.nome, login: e.target.value, senha: usuario.senha };
              setUsuario(novoLogin);
              }
            }
            required
          />
        </Form.Item>

        <Form.Item label="Nova Senha">
          {
            getFieldDecorator('senha', 
            {
              rules: [
                { required: true, message: 'Por Favor, insira uma senha!'},
                { min: 4, message: 'Por Favor, insira uma senha com mais de 4 dígitos!'},
                { validator: validarSenha }
              ],
            })(
              <Input.Password
                prefix={<Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />}
                placeholder="Nova Senha"
                value={usuario.senha}                
                onChange={e => {
                  const novaSenha = { nome: usuario.nome, login: usuario.login, senha: e.target.value };
                  setUsuario(novaSenha);
                  }
                }
              />
              )
          }
        </Form.Item>

        <Form.Item label="Confirmar Senha">
          {
            getFieldDecorator('confirmarSenha', {
              rules: [
                { required: true, message: 'Por Favor, Confirme sua senha!'},
                { min: 4, message: 'A senha digitada é menor que 4 dígitos!'},                
                { validator: compararPrimeiraSenha }
                
              ],
            })(
              <Input.Password
                prefix={<Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />}
                placeholder="Confirmar Senha"
              />
            )
          }
          
        </Form.Item>


        

        <div style={{ height: "15px" }}></div>
        <Button
          type="create"
          icon="check"
          style={{ right: "-36%" }}
          htmlType="submit"
          onClick={editarPerfil}
        >
          {" "}
          Confirmar{" "}
        </Button>
      </Form>

    </Modal>
  );
}

const EditarPerfil = Form.create({ name: 'EditarPerfil'})(EditarPerfilForm);

const ValidarSenhaForm = ({ visivel, userName, hideModal, setAuth, ...props }) => {
  const { getFieldDecorator } = props.form;



  function verifica() {
    console.log("tem que fazer a requisição");

    setAuth(true);
  }

  return (
    <Modal
      visible={visivel}
      footer={null}
      onCancel={() => hideModal()}
      centered
    >
      <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
      >
        {" "}
        Validar Senha
        {" "}
      </Paragraph>
      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="lock" />
      </Divider>

      <Form style={{ marginTop: 10 }}>
        <Form.Item>
          {
            getFieldDecorator('senha')(
              <Input.Password
                placeholder="Senha" prefix={
                  <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                }
              />
            )
          }
        </Form.Item>
        <div style={{ height: "15px" }}></div>
        <Button
          type="create"
          icon="check"
          style={{ right: "-36%" }}
          htmlType="submit"
          onClick={verifica}
        >
          {" "}
          Confirmar{" "}
        </Button>
      </Form>

    </Modal>
  );
}

const ValidarSenha = Form.create({ name: 'ValidarSenha' })(ValidarSenhaForm);

export default function Navbar() {
  //Modal Editar Perfil
  const [modal, setModal] = useState(false);


  //Autentificação de dois fatores caso deseje usar só mudar o useState pra false
  const [autenticado, setAuth] = useState(true);
  //Editar Nome, login e senha
  const [usuario, setUsuario] = useState({
    nome: '',
    login: '',
    senha: ''
  });

  //Modal
  const showModal = () => {
    axios
      .get("/funcionarios/"+getID())
      .then(res => {

        const novoUsuario = {
          nome: res.data.nome,
          login: res.data.login,
          senha: usuario.senha
        };
        setUsuario(novoUsuario);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });

    setModal(true);
  }

  const hideModal = () => {
    setModal(false);
  }


  
  //Editar Perfil
  const editarPerfil = () => {
    axios
    .put("/funcionarios/"+getID(), usuario)
      .then(() => {
        console.log(usuario)
        sucess();
        hideModal();
      })
      .catch(err => {
        console.log(err);

        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });

  }

  //Menu
  const menu = (
    <Menu onClick={() => showModal()}>
      <Menu.Item key="1">
          <Icon type="user" />
          Editar Perfil
      </Menu.Item>
      <Menu.Item key="2">
        <Link to="" onClick={() => signOut()}>
          <Icon type="logout" />
          &nbsp;
          Logout
        </Link>
      </Menu.Item>

    </Menu>
  );


  function signOut() {
    logout();
    window.location.href = '/login';
  }

  return (
    <div>
      <Header className="menuHeader">
        <div id="components-dropdown-demo-dropdown-button">
          <Dropdown overlay={menu} placement="bottomRight">
            <Button style={{ borderWidth: 1.5 }}>
              <Icon type="menu" />Menu
          </Button>
          </Dropdown>

        </div>
      </Header>

      {
        autenticado
          ? (
            <EditarPerfil
              visivel={modal}
              usuario={usuario}
              hideModal={hideModal}
              setUsuario={setUsuario}
              editarPerfil={editarPerfil}
            />
          )
          : (
            <ValidarSenha
              visivel={modal}
              hideModal={hideModal}
              userName={usuario.login}
              setAuth={setAuth}
            />
          )
      }
    </div>
  );
}
