import React, { useState, useEffect } from "react";
import {
  Row,
  List,
  Icon,
  Input,
  Modal,
  Form,
  Button,
  Divider,
  Tag,
  Popover,
  Typography,
  Upload,
  message
} from "antd";

import { SketchPicker } from 'react-color';
import axios from "axios";
import "./familiaProdutos.css";
import {
  err503,
  err401,
  errGeneral,
  sucess,
  successAdicionar
} from "../../services/messages";


const { Paragraph, Title, Text } = Typography;
const { confirm } = Modal;

export default function FamiliaProdutos() {
  //CATEGORIAS
  const [nome, setNome] = useState([]);
  const [cor, setCor] = useState('');
  const [descricao, setDescricao] = useState([]);
  const [categorias, setCategorias] = useState([]);
  const [categoriasFiltered, setCategoriasFiltered] = useState([]);
  //FAMILIA DE PRODUTO
  const [visibleModalNovaFamilia, setVisibleModalNovaFamilia] = useState(false);
  //EDITAR FAMILIA
  const [editFamilia, setEditFamilia] = useState([]);
  const [visibleModalEditFamilia, setVisibleModalEditFamilia] = useState(false);
  const [nomeEdit, setNomeEdit] = useState([]);
  const [corEdit, setCorEdit] = useState([]);
  const [descricaoBasicaEdit, setDescricaoBasicaEdit] = useState("");
  //FOTOS
  const [newFotoProduto, setnewFotoProduto] = useState([]);
  const [FotoProduto, setFotoProduto] = useState("");
  const [state, setState] = useState(true);

  useEffect(() => {
    axios
      .get("/categorias")
      .then(res => {
        setCategorias(res.data);
        setCategoriasFiltered(res.data);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  const filterCategorias = value => {
    setCategoriasFiltered(
      categorias.filter(
        r =>
          (r.nome && r.nome.toLowerCase().includes(value.toLowerCase())) ||
          (r.descricaoBasica &&
            r.descricaoBasica.toLowerCase().includes(value.toLowerCase()))
      )
    );
  };


  const handleChangeComplete = color => {
    setCor(color.hex)
  };

  //função para poder editar a categoria de um produto feita pelo guilherme
  const editarCategoria = e => {
    e.preventDefault();

    let familiaEditada = {};
    familiaEditada.id = editFamilia._id;
    familiaEditada.nome = nomeEdit;
    familiaEditada.cor = corEdit;
    //familiaEditada.imagem = imagemEdit;
    familiaEditada.descricaoBasica = descricaoBasicaEdit;


    confirm({
      title: "Editar familia",
      content: "Esta ação é definitiva, confirma a edição?",
      okType: "danger",
      okText: "Editar",
      cancelText: "Cancelar",
      onOk() {
        axios
          .put("/categorias/", familiaEditada)
          .then(() => {
            setState(!state);
            sucess();
            hideModalEditFamilia();
          })
          .catch(err => {

            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  //fim da função de edita familia

  const excluirCategoria = categoria => {
    confirm({
      title: `Excluir ${categoria.nome}`,
      content:
        "Esta ação é definitiva, confirma a exclusão? Não esqueça que os produtos desta categoria deverão ser apagados ou trocados de catergoria.",
      okType: "danger",
      okText: "Excluir",
      cancelText: "Cancelar",
      onOk() {
        axios
          .delete(`/categorias/${categoria._id}`)
          .then(() => {
            setState(!state);
            sucess();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  const criarNovaFamilia = e => {
    e.preventDefault();
    const data = new FormData(e.target);


    let nome = data.get("nome");
    let cor = data.get("cor");
    let imagem = FotoProduto;
    let descricaoBasica = data.get("descricaoBasica");

    confirm({
      title: `Confirmar`,
      content:
        "As informações digitadas estão corretas? Você confirma a criação de uma nova família?",
      okType: "primary",
      okText: "Criar",
      cancelText: "Cancelar",
      onOk() {
        axios
          .post("/categorias", { nome, cor, descricaoBasica, imagem })
          .then(() => {
            setState(!state);
            successAdicionar("Familia de Produto");
            hideModalNovaFamilia();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) {
              err503();
            } else if (err.response && err.response.status === 401) {
              err401();
            } else {
              errGeneral();
            }
          });
      }
    });
  };

  // função pra upload imagem funcionando
  const novaFamiliaImagem = () => {

    const upImagem = new FormData();
    upImagem.append("api_key", "784737987534174");
    upImagem.append("timestamp", Date.now() / 1000);
    upImagem.append("upload_preset", "hatsxc2p");
    upImagem.append("file", newFotoProduto[0]);

    axios
      .post("https://api.cloudinary.com/v1_1/haken/raw/upload", upImagem, {
        headers: { "X-Requested-With": "XMLHttpRequest" }
      })
      .then(res => {
        //console.log("foto", res.data);
        setFotoProduto(res.data.secure_url);
      })
      .catch(() => {
        setFotoProduto("");
      });
  };
  //fim da função de upload

  //declaração da fumção de upload
  const beforeupImagem = file => {
    if (file.type === "image/jpeg" || file.type === "image/png") {
      const arquivos = [file];
      setnewFotoProduto(arquivos);
      return true;
    }

    message.error("Extensão Inválida! Permitido apenas imagens");
    setnewFotoProduto([]);
    return false;
  };
  //fim da declaração

  const showModalNovaFamilia = () => {
    setNome("");
    setCor("");
    setDescricao("");
    setVisibleModalNovaFamilia(true);
  };

  const hideModalNovaFamilia = () => {
    setNome("");
    setCor("");
    setDescricao("");
    setVisibleModalNovaFamilia(false);
    setnewFotoProduto([]);
  };

  const showModalEditFamilia = item => {
    setCorEdit(item.cor);
    setNomeEdit(item.nome);
    setDescricaoBasicaEdit(item.descricaoBasica);
    //setImagemEdit(item.imagem);
    setEditFamilia(item);
    setVisibleModalEditFamilia(true);
  };

  const hideModalEditFamilia = () => {
    setNomeEdit("");
    setCorEdit("");
    //setImagemEdit("");
    //setDescricaoBasica('')
    setVisibleModalEditFamilia(false);
  };

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Familia de Produtos{" "}
      </Paragraph>
      <Divider
        style={{
          fontSize: 20,
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="apartment" />
      </Divider>

      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <Input.Search
          placeholder="Procurar por descrição ou nome"
          onChange={e => filterCategorias(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />

        <Button
          onClick={() => showModalNovaFamilia()}
          type="primary"
          icon="plus"
          style={{ float: "right" }}
        >
          {" "}
          Nova Familia{" "}
        </Button>
      </Row>
      <Modal
        visible={visibleModalNovaFamilia}
        footer={null}
        onCancel={() => hideModalNovaFamilia()}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Cadastro de Nova Familia{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="file" />
        </Divider>
        <Form style={{ marginTop: 10 }} onSubmit={criarNovaFamilia}>
          <Form.Item label='Nome'>
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Nome da Familia"
              name="nome"
              value={nome}
              onChange={e => setNome(e.target.value)}
            />
          </Form.Item >
          <Form.Item label='Descrição'>
            <Input.TextArea
              placeholder="Descrição da Familia"
              autosize={{ minRows: 2 }}
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              name="descricaoBasica"
              value={descricao}
              onChange={e => setDescricao(e.target.value)}
            />
          </Form.Item>

          <Form.Item label='Cor'>

            <SketchPicker color={cor} onChange={handleChangeComplete}></SketchPicker>


            <Input
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Cor"
              name="cor"
              value={cor}
              onChange={e => setCor(e.target.value)}
            />
          </Form.Item>
          <Form.Item label='Imagem'>
            <Upload
              beforeUpload={beforeupImagem}

              onRemove={(remove) => {
                console.log("alou");
                
                setnewFotoProduto([]);
              }
              }
              customRequest={novaFamiliaImagem}
              fileList={newFotoProduto}
              acecept="image/*"
            >
              <Button icon="upload"> Upload </Button>
            </Upload>
          </Form.Item>

          <div style={{ height: "15px" }}></div>

          <Button
            type="create"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
          >
            {" "}
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>
      {/* final 1° modal*/}

      {/* Inicio da 2° modal*/}

      <Modal
        visible={visibleModalEditFamilia}
        footer={null}
        onCancel={() => hideModalEditFamilia()}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Editar Familia{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="file" />
        </Divider>
        <Form style={{ marginTop: 10 }} onSubmit={editarCategoria}>
          <Form.Item
            label="Nome da família"
          >
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Nome da Familia"
              name="nome"
              value={nomeEdit} //edição "_id"
              onChange={e => setNomeEdit(e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Descrição">
            <Input.TextArea
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Descrição da Família"
              name="descricaoBasica"
              value={descricaoBasicaEdit} //edição "_id"
              onChange={e => setDescricaoBasicaEdit(e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Cor">
            <Input
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Cor"
              name="cor"
              value={corEdit} //edição "_id"
              onChange={e => setCorEdit(e.target.value)}
            />
          </Form.Item>

          <div style={{ height: "15px" }}></div>

          <Button
            type="create"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
          >
            {" "}
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>
      {/* final 2° modal */}

      {/* forma pratica pra usar caso queira usar pra poder fazer a mesma função em outros locais */}
      <List
        itemLayout="vertical"
        size="large"
        style={{ marginTop: 20 }}
        pagination={{ pageSize: 3 }}
        dataSource={categoriasFiltered}
        renderItem={(item, text, record) => (
          <List.Item
            key={item._id}
            actions={[
              <Popover content="Editar">
                <Icon
                  onClick={() => showModalEditFamilia(item)}
                  type="edit"
                  theme="twoTone"
                  twoToneColor="orange"
                />
              </Popover>,

              <Popover content="Exclusão">
                <Icon
                  onClick={() => excluirCategoria(item)}
                  type="delete"
                  theme="twoTone"
                  twoToneColor="#EB3B5A"
                />
              </Popover>
            ]}
            extra={
              <img
                style={{ maxWidth: 200, maxHeight: 200 }}
                alt={item.nome}
                src={item.imagem}
              />
            }
          >
            <Typography>
              <Row style={{ display: "flex" }}>
                <Title level={4} style={{ marginRight: 14 }}>
                  {" "}
                  {item.nome}{" "}
                </Title>
                <Text strong>
                  {" "}
                  <Tag color={item.cor}> {item.cor} </Tag>{" "}
                </Text>
              </Row>
              <Paragraph
                type="secondary"
                ellipsis={{ rows: 3, expandable: true }}
              >
                {" "}
                {item.descricaoBasica}{" "}
              </Paragraph>
            </Typography>
          </List.Item>
        )}
      />
    </>
  );
}
