import React, { Component } from "react";
import { Redirect, Switch, Route, BrowserRouter } from "react-router-dom";
import { ConfigProvider, Empty } from "antd";

import ptBR from "antd/lib/locale-provider/pt_BR";
import moment from "moment";

import Login from "./pages/login/login";
import MainLayout from "./components/home";

import FamiliaProdutos from "./pages/familiaProdutos/familiaProdutos";
import ListagemProdutos from "./pages/produtos/listagemProdutos";
import Bicos from "./pages/bicos/bicos";
import Funcionario from "./pages/funcionario/funcionario";
import Chamados from "./pages/chamados/chamados";
import Usuarios from "./pages/usuarios/usuarios";
import Orcamentos from "./pages/orcamento/orcamento";
import Marcas from "./pages/marcas/marcas";
import Estatisticas from "./pages/estatísticas/estatísticas"

import "moment/locale/pt-br";
import "./App.css";

moment.locale("pt-BR");

const EmptyPage = () => <Empty description="Não há dados" />;
const PrivateRoute = ({ component: Component, ...rest }) => {
  const redirectToLogin = (props) => {
    return (
      <Redirect
        to={{
          pathname: "/login",
          state: { from: props.location }
        }}
      />
    )
  }
  const openPages = (props) => {
    return <Component {...props} />;
  }
  const hasToken = localStorage.getItem("solus_token");
  return (
    <Route
      {...rest}
      render={props => hasToken ? openPages(props) : redirectToLogin(props)}
    />
  );
};

function Home() {
  return (
    <BrowserRouter>
      <MainLayout>
        <Switch>
          <PrivateRoute
            path="/familiaProdutos"
            exact
            component={FamiliaProdutos}
          />
          <PrivateRoute
            path="/listagemProdutos"
            exact
            component={ListagemProdutos}
          />
          <PrivateRoute path="/bicos" exact component={Bicos} />
          <PrivateRoute path="/chamados" exact component={Chamados} />
          <PrivateRoute path="/funcionario" exact component={Funcionario} />
          <PrivateRoute path="/usuarios" exact component={Usuarios} />
          <PrivateRoute path="/orcamentos" exact component={Orcamentos} />
          <PrivateRoute path="/marcas" exact component={Marcas} />
          <PrivateRoute path="/estatisticas" exact component={Estatisticas} />
        </Switch>
      </MainLayout>
    </BrowserRouter>
  );
}

class App extends Component {
  render() {
    return (
      <ConfigProvider locale={ptBR} renderEmpty={EmptyPage}>
        <BrowserRouter>
          <Switch>
            <Route path="/home" exact component={Home} />
            <Route path="/*" exact component={Login} />
          </Switch>
        </BrowserRouter>
      </ConfigProvider>
    );
  }
}

export default App;
