import React, { useState, useEffect } from "react";
import {
  Row,
  Icon,
  Input,
  Modal,
  Button,
  Divider,
  Typography,
  Table,
  Form,
  Popover,
  Select,
  message,
} from "antd";

import './bico.css';
import axios from "axios";
import { err503, err401, errGeneral, sucess } from "../../services/messages";


// import './familiaProdutos.css';
const { Option } = Select;
const { Paragraph } = Typography;
const { confirm } = Modal;

const Bicos = props => {
  const { getFieldDecorator } = props.form;
  const [bicos, setBicos] = useState([]);
  const [bicosFiltered, setBicosFiltered] = useState([]);
  const [visibleModalBico, setVisibleModalBico] = useState(false);
  const [visibleModalMarca, setVisibleModalMarca] = useState(false);
  const [state, setState] = useState(true);
  const [marcas, setMarcas] = useState([]);
  const [stateMarca, setStateMarca] = useState(true);
  const [inputState, setStateInput] = useState(true);

  // Variáveis de estado de edição de bicos
  const [editarCor, setEditarCor] = useState("");
  const [editarMarca, setEditarMarca] = useState("");
  const [editarVazaoMinima, setEditarVazaoMinima] = useState([]);
  const [editarVazaoMaxima, setEditarVazaoMaxima] = useState([]);
  const [visibleModalEditarBico, setVisibleModalEditarBico] = useState(false);
  const [editarBicoId, setEditarBicoId] = useState("");


  useEffect(() => {
    axios
      .get("/bicos")
      .then(res => {
        res.data.sort((a, b) => a.marca.nome.toLowerCase() > b.marca.nome.toLowerCase() ? 1 : -1);

        setBicos(res.data);
        setBicosFiltered(res.data);
        setState(true);
        setStateInput(true);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  useEffect(() => {
    axios
      .get("/marcas")
      .then(res => setMarcas(res.data))

  }, [stateMarca]);

  const filterBicos = value => {
    setBicosFiltered(
      bicos.filter(r =>
        (r.marca.nome && r.marca.nome.toLowerCase().includes(value.toLowerCase())) ||
        (r.cor && r.cor.toLowerCase().includes(value.toLowerCase())) ||
        (value >= r.vazaoMinima && value <= r.vazaoMaxima)
      )
    );
  };

  const excluirBico = bico => {
    confirm({
      title: `Excluir ${bico.marca}`,
      content: "Esta ação é definitiva, confirma a exclusão? ",
      okType: "danger",
      okText: "Excluir",
      cancelText: "Cancelar",
      onOk() {
        axios
          .delete(`/bicos/${bico._id}`)
          .then(() => {
            setState(false);
            sucess();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  useEffect(() => {
  }, [inputState]);

  const adicionarBicos = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        if (Number(values.vazaoMaxima) > Number(values.vazaoMinima)) {
          confirm({
            title: `Adicionar Bico`,
            content: "Tem certeza que as informações estão corretas? ",
            okType: "danger",
            okText: "Adicionar",
            cancelText: "Cancelar",
            onOk() {
              axios
                .post("/bicos/", values)
                .then(() => {
                  setState(false);
                  props.form.setFields({
                    cor: {
                      value: ""
                    },
                    vazaoMaxima: {
                      value: ""
                    },
                    vazaoMinima: {
                      value: ""
                    },
                    marca: {
                      placeholder: ""
                    }
                  });
                  hideModalBico();
                  sucess();
                })
                .catch(err => {
                  if (err.response && err.response.status === 503) err503();
                  else if (err.response && err.response.status === 401) err401();
                  else errGeneral();
                });
            }
          });
        } else errorVazao();
      }
    });
  };

  const editarBico = e => {
    e.preventDefault();

    let bico = {};
    bico._id = editarBicoId;
    bico.marca = editarMarca;
    bico.cor = editarCor;
    bico.vazaoMinima = Math.abs(editarVazaoMinima);
    bico.vazaoMaxima = Math.abs(editarVazaoMaxima);

    if (Number(bico.vazaoMaxima) > Number(bico.vazaoMinima)) {
      confirm({
        title: "Editar Bico",
        content: "Esta ação é definitiva, confirmar a edição ?",
        okType: "danger",
        okText: "Confirmar",
        cancelText: "Cancelar",
        onOk() {
          axios
            .put("/bicos/" + bico._id, bico)
            .then(() => {
              setState(false);
              sucess();
              hideModalEditarBico();
            })
            .catch(err => {
              if (err.response && err.response.status === 503) err503();
              else if (err.response && err.response.status === 401) err401();
              else errGeneral();
            });
        }
      });
    } else errorVazao();
  };

  const showModalBico = () => {
    setVisibleModalBico(true);
  };

  const hideModalBico = () => {
    setVisibleModalBico(false);
  };

  const showModalMarca = () => {
    setVisibleModalMarca(true);
  };

  const hideModalMarca = () => {
    setVisibleModalMarca(false);
  };

  const adicionarMarca = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    let marca = {};
    marca.nome = data.get("marca");

    confirm({
      title: `Adicionar a marca ${marca.nome}`,
      content: "Tem certeza que as informações estão corretas? ",
      okType: "success",
      okText: "Adicionar",
      cancelText: "Cancelar",
      onOk() {
        axios
          .post("/marcas", marca)
          .then(() => {
            setStateMarca(false);
            sucess();
            data.set("marca", "");
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  const hideModalEditarBico = () => {
    setEditarMarca('');
    setVisibleModalEditarBico(false);
  };

  const showModalEditarBico = item => {
    setEditarCor(item.cor);
    setEditarMarca(item.marca);
    setEditarVazaoMinima(item.vazaoMinima);
    setEditarVazaoMaxima(item.vazaoMaxima);
    setEditarBicoId(item._id)
    setVisibleModalEditarBico(true);
  };

  const columns = [
    {
      title: "Marca",
      dataIndex: "marca.nome",
      width: "15%",
      key: "marca.nome",
      sorter: (a, b) => a.marca.nome.localeCompare(b.marca.nome)
    },
    {
      title: "Cor",
      dataIndex: "cor",
      width: "10%",
      key: "cor",
      sorter: (a, b) => a.cor.localeCompare(b.cor)
    },
    {
      title: "Vazão Mínima ( ml / min. )",
      className: 'column-center',
      dataIndex: "vazaoMinima",
      width: "15%",
      key: "vazaoMinima",
      sorter: (a, b) => a.vazaoMinima - b.vazaoMinima
    },
    {
      title: "Vazão Máxima ( ml / min. )",
      className: 'column-center',
      dataIndex: "vazaoMaxima",
      width: "15%",
      key: "vazaoMaxima",
      sorter: (a, b) => a.vazaoMaxima - b.vazaoMaxima
    },
    {
      title: "Opções",
      width: "10%",
      className: 'column-center',
      key: "acao",
      render: (text, record) => (
        <span>
          <Popover content="Editar">
            <Icon
              onClick={() => showModalEditarBico(record)}
              type="edit"
              theme="twoTone"
              twoToneColor="orange"
            />
          </Popover>

          <Divider
            style={{ marginLeft: 15, marginRight: 15 }}
            type="vertical"
          />

          <Popover content="Excluir">
            <Icon
              onClick={() => excluirBico(record)}
              type="delete"
              theme="twoTone"
              twoToneColor="#EB3B5A"
            />
          </Popover>
        </span>
      )
    }
  ];

  const errorVazao = () => {
    message.error('Vazão máxima é menor que vazão mínima');
  };

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Bicos{" "}
      </Paragraph>

      <Divider
        style={{
          fontSize: 20,
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="apartment" />
      </Divider>

      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>
        <Input.Search
          placeholder="Procurar por Marca, Cor ou Vazão"
          onChange={e => filterBicos(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />

        <Button
          onClick={() => showModalBico()}
          type="primary"
          icon="plus"
          style={{ float: "right" }}
        >
          {" "}
          Novo Bico{" "}
        </Button>
      </Row>

      <Table
        style={{ marginTop: 20 }}
        rowKey="_id"
        columns={columns}
        dataSource={bicosFiltered}
      />

      <Modal
        visible={visibleModalBico}
        footer={null}
        onCancel={() => hideModalBico()}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Cadastro de Bico{" "}
        </Paragraph>

        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="highlight" />
        </Divider>

        <Form style={{ marginTop: 10 }} onSubmit={adicionarBicos}>
          <Form.Item label='Cor do bico'>
            {getFieldDecorator('cor', {
              rules: [{ required: true, message: 'Por favor informe a cor do bico.' }],
            })(
              <Input
                prefix={<Icon type="bg-colors" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="Cor do Bico"
              />,
            )}
          </Form.Item>

          <Form.Item label='Vazão Mínima ( ml / min. )'>

            {getFieldDecorator('vazaoMinima', {
              rules: [{ required: true, message: 'Por favor, informe a vazão mínima.' }],
            })(
              <Input
                type="number"
                prefix={<Icon type="filter" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="Vazão Mínima"
                step="any"
                min="0"
              />,
            )}
          </Form.Item>
          <Form.Item label='Vazão Máxima ( ml / min. )'>
            {getFieldDecorator('vazaoMaxima', {
              rules: [{ required: true, message: 'Por favor, informe a vazão máxima.' }]
            })(
              <Input
                type="number"
                step="any"
                prefix={<Icon type="funnel-plot" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="Vazão Máxima"
                min="0"
              />,
            )}
          </Form.Item>
          <Form.Item label='Marca do bico'>
            {getFieldDecorator('marca', {
              rules: [{ required: true, message: 'Por favor selecione marca.' }],
            })(
              <Select
                mode="unique"
                showSearch
                style={{ width: 350 }}
                placeholder="Selecione a Marca"
                optionFilterProp="dropdownRender"
                filterOption={(input, option) =>
                  option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
              >
                {marcas.map((valor, index) => (

                  <Option value={valor._id} key={index}>{valor.nome}</Option>

                ))}
              </Select>
            )}
            <Button
              onClick={showModalMarca}
              icon="plus"
              style={{ right: "-5.3%" }}
            >
              Marca
            </Button>
          </Form.Item>

          <Button
            type="primary"
            icon="check"
            style={{ right: "-36%", display: "flex", marginTop: "45px" }}
            htmlType="submit"
          >
            Confirmar{" "}
          </Button>


        </Form>

      </Modal>

      <Modal
        visible={visibleModalMarca}
        onCancel={() => hideModalMarca()}
        centered
        footer={null}
      >
        <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
          Cadastro de Marca
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="flag" />
        </Divider>

        <Form style={{ marginTop: 10 }} onSubmit={adicionarMarca}>
          <Form.Item label="Nome da marca">

            <Input
              prefix={
                <Icon type="bg-colors" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Nome da marca"
              name="marca"
              required
            />
          </Form.Item>

          <Button
            type="danger"
            icon="check"
            style={{ right: "-16%" }}
            htmlType="reset"
            onClick={() => hideModalMarca()}
          >
            Cancelar{" "}
          </Button>

          <Button
            type="primary"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
            onClick={() => hideModalMarca()}
          >
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>


      <Modal
        visible={visibleModalEditarBico}
        onCancel={() => hideModalEditarBico()}
        centered
        footer={null}
      >
        <Paragraph
          style={{ fontSize: 22, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Editar Bico{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "40%",
            width: "40%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="highlight" />
        </Divider>

        <Form style={{ marginTop: 10 }} onSubmit={editarBico}>
          <Form.Item label="Cor do bico">
            <Input
              prefix={
                <Icon type="bg-colors" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Cor do Bico"
              value={editarCor}
              onChange={e => setEditarCor(e.target.value)}
            />
          </Form.Item>

          <Form.Item label="Vazão Mínima ( ml / min. )">
            <Input
              prefix={
                <Icon type="filter" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Vazão Mínima"
              min="0"
              value={editarVazaoMinima}
              onChange={e => setEditarVazaoMinima(e.target.value)}
            />
          </Form.Item>

          <Form.Item label="Vazão Máxima ( ml / min. )">
            <Input
              prefix={
                <Icon type="funnel-plot" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Vazão Máxima"
              value={editarVazaoMaxima}
              min="0"
              onChange={e => setEditarVazaoMaxima(e.target.value)}
            />
          </Form.Item>

          <Form.Item label='Marca do bico'>
            <Select
              mode="unique"
              showSearch
              style={{ width: 350 }}
              placeholder="Selecione a Marca"
              optionFilterProp="dropdownRender"

              value={editarMarca._id} // setando valor de default -- não funcionando
              onChange={e => { setEditarMarca({ _id: e }) }} // coloquei isso mas ta dando erro


              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {marcas.map((valor, index) => (
                <Option value={valor._id} key={index}>
                  {valor.nome}
                </Option>
              ))}
            </Select>

            <Button
              onClick={showModalMarca}
              icon="plus"
              style={{ right: "-5.3%" }}
            >
              Marca
            </Button>
          </Form.Item>

          <Button
            type="primary"
            icon="check"
            style={{ right: "-36%", marginTop: 45 }}
            htmlType="submit"
          >
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>
    </>
  );
};

const WrappedNormalBicoForm = Form.create({ name: "normal_bicos" })(Bicos);
const WrappedNormalBicoFormEdit = Form.create({ name: "normal_bicos_edit" })(
  Bicos
);
export default (WrappedNormalBicoForm, WrappedNormalBicoFormEdit);

