import React, { useState, useEffect } from "react";
import {
  Row,
  Icon,
  Input,
  Modal,
  Button,
  Divider,
  Typography,
  Table,
  Form,
  Popover
} from "antd";

import { getToken } from "../../services/auth";
import axios from "axios";
import {
  err503,
  err401,
  errGeneral,
  sucess,
  successAdicionar
} from "../../services/messages";

const { Paragraph } = Typography;
const { confirm } = Modal;

export default function Funcionarios() {
  //FUNCIONARIO
  const [funcionario, setFuncionario] = useState({});
  const [funcionarioFiltered, setFuncionarioFiltered] = useState([]);
  //LOADING
  const [visibleModalFuncionario, setVisibleModalFuncionario] = useState(false);
  //EDIÇÃO FUNCIONARIO
  const [nomeEdit, setNomeEdit] = useState([]);
  const [idEdit, setIdEdit] = useState("");
  const [modalEdit, setModalEdit] = useState(false);
  //CADASTRO
  const [nome, setNome] = useState([]);
  const [login, setLogin] = useState([]);
  const [senha, setSenha] = useState([]);
  const [senha2, setSenha2] = useState([]);
  const [state, setState] = useState(true);


  useEffect(() => {
    axios
      .get("/funcionarios", { headers: { token: getToken() } })
      .then(res => {

        let filteredWithKey = res.data.map((item, key) => {
          let returnData = item;
          returnData.key = key;
          return returnData;
        });
        setFuncionarioFiltered(filteredWithKey);
        setFuncionario(res.data);
        setState(true);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  const filterFuncionario = value => {
    setFuncionarioFiltered(
      funcionario.filter(
        r =>
          (r.nome && r.nome.toLowerCase().includes(value.toLowerCase())) ||
          (r.login && r.login.toLowerCase().includes(value.toLowerCase()))
      )
    );
  };

  const adicionarFuncionario = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    let funcionario = {};

    funcionario.isAdm = false;
    funcionario.nome = data.get("nome");
    funcionario.login = data.get("login");

    let pass1 = data.get("senha1");
    let pass2 = data.get("senha2");

    if (pass1 === pass2) {
      funcionario.senha = pass1;
      confirm({
        title: "Adicionar funcionario",
        content: "Tem certeza que as informações estão corretas? ",
        okText: "Adicionar",
        cancelText: "Cancelar",
        onOk() {
          axios
            .post("/funcionarios/", funcionario)
            .then(() => {
              setState(false);
              hideModalFuncionario();
              successAdicionar("Funcionario");
            })
            .catch(err => {
              if (err.response && err.response.status === 503) err503();
              else if (err.response && err.response.status === 401) err401();
              else errGeneral();
            });
        }
      });
    } else {
      confirm({
        title: "As senhas não se coincidem",
        content: "Por favor, tente novamente",
        onCancel() {
          hideModalFuncionario();
        }
      });
    }
  };

  const excluirFuncionario = funcionario => {
    confirm({
      title: `Excluir ${funcionario.nome}`,
      content: "Esta ação é definitiva, confirma a exclusão? ",
      okType: "danger",
      okText: "Excluir",
      cancelText: "Cancelar",
      onOk() {
        axios
          .delete(`/funcionarios/${funcionario._id}`)
          .then(() => {
            setState(false);
            sucess();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  const editarFuncionario = e => {
    e.preventDefault();
    const nome = nomeEdit
    confirm({
      title: "Editar membro",
      content: "Tem certeza que as informações estão corretas? ",
      okText: "Confirmar",
      cancelText: "Cancelar",
      onOk() {
        axios
          .put(`/funcionarios/${idEdit}`, { nome })
          .then(() => {
            setState(!state);

            sucess();
            hideModalEdit();
          })
          .catch(err => {

            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };


  const showModalFuncionario = () => {
    setNome("");
    setLogin("");
    setSenha("");
    setSenha2("");
    setVisibleModalFuncionario(true);
  };

  const hideModalFuncionario = () => {
    setNome("");
    setLogin("");
    setSenha("");
    setSenha2("");
    setVisibleModalFuncionario(false);
  };

  const showEdit = e => {
    setIdEdit(e._id);
    setNomeEdit(e.nome);
    setModalEdit(true);
  };

  const hideModalEdit = () => {
    setNomeEdit("");
    
    
    setModalEdit(false);
  };

  const columns = [
    {
      title: "Nome",
      dataIndex: "nome",
      width: "20%",
      key: "nome",
      //render: text => <a>{text}</a>
    },
    {
      title: "Login",
      dataIndex: "login",
      width: "12%",
      key: "login"
    },
    {
      title: "Ação",
      width: "17%",
      key: "acao",
      render: (text, record) => (
        <span>
          <Popover content="Edição">
            <Icon
              type="edit"
              onClick={() => showEdit(record)}
              theme="twoTone"
              twoToneColor="orange"
            />
          </Popover>

          <Divider
            style={{ marginLeft: 15, marginRight: 15 }}
            type="vertical"
          />

          <Popover content="Exclusão">
            <Icon
              onClick={() => excluirFuncionario(record)}
              type="delete"
              theme="twoTone"
              twoToneColor="#EB3B5A"
            />
          </Popover>
        </span>
      )
    }
  ];

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Membros{" "}
      </Paragraph>

      <Divider
        style={{
          fontSize: 20,
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="apartment" />
      </Divider>

      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>

        <Input.Search
          placeholder="Procurar por nome ou login"
          onChange={e => filterFuncionario(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />

        <Button
          onClick={() => showModalFuncionario()}
          type="primary"
          icon="plus"
          style={{ float: "right" }}
        >
          {" "}
          Novo Membro{" "}
        </Button>
      </Row>
      <Table
        style={{ marginTop: 20 }}
        columns={columns}
        dataSource={funcionarioFiltered}
      />

      {/* modal cadastro */}
      <Modal
        visible={visibleModalFuncionario}
        footer={null}
        onCancel={() => hideModalFuncionario()}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Cadastro de Membro{" "}
        </Paragraph>

        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 20,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="user-add" />
        </Divider>

        <Form style={{ marginTop: 10 }} onSubmit={adicionarFuncionario}>
          <Form.Item label="Nome completo">
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Nome completo"
              name="nome"
              value={nome}
              onChange={e => setNome(e.target.value)}
              required
            />
          </Form.Item>

          <Form.Item label="Login">
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Login"
              name="login"
              value={login}
              onChange={e => setLogin(e.target.value)}
              required
            />
          </Form.Item>

          <Form.Item label="Senha">
            <Input
              prefix={<Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Digite a senha"
              type="password"
              name="senha1"
              value={senha}
              onChange={e => setSenha(e.target.value)}
              required
            />
          </Form.Item>

          <Form.Item hasFeedback label="Confirmar senha">
            <Input
              prefix={<Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Digite a senha novamente"
              type="password"
              name="senha2"
              value={senha2}
              onChange={e => setSenha2(e.target.value)}
              required
            />
          </Form.Item>

          <Button
            type="primary"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
          >
            {" "}
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>

      {/* modal edição */}
      <Modal
        visible={modalEdit}
        footer={null}
        onCancel={hideModalEdit}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Editar Membro{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="file" />
        </Divider>
        <Form style={{ marginTop: 10 }} onSubmit={editarFuncionario}>
          <Form.Item label="Nome Completo">
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Digite o nome completo"
              name="nome"
              value={nomeEdit}
              onChange={e => setNomeEdit(e.target.value)}
            />
          </Form.Item>
          <Button
            type="create"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
          >
            {" "}
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>
    </>
  );
}
