import React, { useState, useEffect } from "react";
import {
  Modal,
  Icon,
  Divider,
  Row,
  Input,
  Typography,
  Table,
  Tag,
  Descriptions,
  Popover,
  Form,
  Button,
  Select,
  Checkbox,

} from "antd";

import axios from "axios";
import { err503, err401, errGeneral } from "../../services/messages";

const { Paragraph } = Typography;
const { Option } = Select;

export default function Chamados() {
  const [chamados, setChamados] = useState({});
  const [modalEdicao, setModalEdicao] = useState(false);
  const [status, setStatus] = useState(false);
  const [id, setId] = useState('');
  const [resposta, setResposta] = useState('');

  //filter
  const [chamadosFiltered, setChamadosFiltered] = useState([]);
  const [habilityFilter, setHabilityFilter] = useState(true);
  const [state, setState] = useState(true);
  const moment = require("moment");

  const openModalEdicao = item => {
    if (item.status === 1) {
      setStatus(true)
    } else
      setStatus(false)

    setId(item._id);
    setResposta(item.resposta)
    setModalEdicao(true)
  }

  const hideModalEdicao = () => {
    setStatus(false);
    setId('');
    setResposta('')
    setModalEdicao(false)
  }

  useEffect(() => {
    axios
      .get("/chamados")
      .then(res => {
        let filteredWithKey = res.data.map((item, key) => {
          let returnData = item;
          returnData.key = key;
          return returnData;
        });
        setChamadosFiltered(filteredWithKey);
        setChamados(res.data);
        setState(true);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);


  const enviarResposta = e => {
    e.preventDefault();

    axios.post('/chamados/resposta', {
      id: id,
      resposta: resposta
    }).then(res => {
      setState(!state)
      hideModalEdicao();

    }).catch(err => {
      if (err.response && err.response.status === 503) err503();
      else if (err.response && err.response.status === 401) err401();
      else errGeneral();
    })

  }

  const filterStatus = value => {    
    setChamadosFiltered(
      chamados.filter(
        r =>
          (r.status === value)
      )
    );
  };


  const filterChamados = value => {
    setChamadosFiltered(
      chamados.filter(
        r =>
          (r.id.nome &&
            r.id.nome.toLowerCase().includes(value.toLowerCase())) ||
          (r.descricao &&
            r.descricao.toLowerCase().includes(value.toLowerCase())) ||

          (r.id.telefoneCel &&
            r.id.telefoneCel.toLowerCase().includes(value.toLowerCase())) ||
          (r.avaliacao &&
            r.avaliacao.toLowerCase().includes(value.toLowerCase())) ||
          (r.avaliacao &&
            r.avaliacao.toLowerCase().includes(value.toLowerCase())) ||
          (r.createdAt &&
            r.createdAt.toLowerCase().includes(value.toLowerCase())) ||
          (r.modeloTrator &&
            r.modeloTrator.toLowerCase().includes(value.toLowerCase())) ||
          (r.createdAt &&
            r.createdAt.toLowerCase().includes(value.toLowerCase()))
      )
    );
  };

  const columns = [
    { title: "Nome do Cliente", dataIndex: "id.nome", key: "id.nome" },
    { title: "Descrição", dataIndex: "descricao", key: "descricao" },
    {
      title: "Situação",
      dataIndex: "status",
      key: "status",
      render: status => {
        let color = "green",
          tag = "aberto";

        if (String(status) === "1") {
          color = "red";
          tag = "fechado";
        } else if (String(status) === "0") {
          color = "orange";
          tag = "em andamento";
        }
        return <Tag color={color}>{tag.toUpperCase()}</Tag>;
      }
    },
    {
      title: "Ação",
      key: "acao",
      dataIndex: "status",
      render: (Status, record) => {
        let texto = '';
        let popText = ''
        let cor = ''
        if (String(Status) === '1') {
          texto = "Já respondido"
          cor = 'green'
          popText = "Vizualizar Resposta"
        }
        else {
          texto = "Responder"
          cor = 'gold'
          popText = "Responder"
        }
        return (
          <span>
            <Popover content={popText}>
              <Tag color={cor} onClick={() => openModalEdicao(record)}> {texto.toUpperCase()}</Tag>
            </Popover>
          </span>
        )
      }
    }
  ];

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Chamados{" "}
      </Paragraph>

      <Divider
        style={{
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="notification" />
      </Divider>

      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>

        <Input.Search
          placeholder="Procurar por nome do cliente ou descrição"
          onChange={e => filterChamados(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />
      </Row>



      <Row >
        <Checkbox
          onChange={({ target }) => {
            console.log(target.checked);
            setHabilityFilter(!target.checked);
            setState(false);
          }}

        >
          Filtrar :
        </Checkbox>


        <Select

          showSearch
          style={{ width: 600 }}
          placeholder="Filtrar por situação"
          optionFilterProp="children"
          autoClearSearchValue="true"
          disabled={habilityFilter}
          onSelect={filterStatus}

          filterOption={(input, option) =>
            option.props.children
              .toLowerCase()
              .indexOf(input.toLowerCase()) >= 0
          }
        >
          <Option value={0} key={0}>
              Aberto
            </Option>
            <Option value={1} key={1}>
            Respondido
            </Option>
        </Select>
      </Row>





      <Table
        style={{ marginTop: 20 }}
        columns={columns}
        dataSource={chamadosFiltered}
        expandedRowRender={record => (
          <div style={{ margin: 0 }}>
            <Descriptions>
              <Descriptions.Item label="Data do envio">
                {moment(record.createdAt).format("LL")}
              </Descriptions.Item>
              <Descriptions.Item label="Modelo do Trator">
                {record.modeloTrator}
              </Descriptions.Item>
              <Descriptions.Item label="Modelo da Plantadeira">
                {record.modeloPlantadeira}
              </Descriptions.Item>
              <Descriptions.Item label="Nota">
                {record.avaliacao}
              </Descriptions.Item>
              <Descriptions.Item label="Contato">
                {record.id.telefoneCel}
              </Descriptions.Item>
              <Descriptions.Item label="CEP">
                {record.localizacao.cep}
              </Descriptions.Item>
            </Descriptions>
          </div>
        )}
      ></Table>

      <Modal
        visible={modalEdicao}
        footer={null}
        onCancel={() => hideModalEdicao()}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Resposta de Chamado{" "}
        </Paragraph>

        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="highlight" />
        </Divider>


        <Form style={{ marginTop: 10 }} onSubmit={enviarResposta}>

          <Form.Item>
            <Input.TextArea
              disabled={status}
              placeholder="Resposta ao chamado"
              autosize={{ minRows: 2 }}
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              name="resposta"
              value={resposta}
              onChange={e => setResposta(e.target.value)}
            />
          </Form.Item>

          <Button
            type="create"
            icon="check"
            style={{ right: "-36%" }}
            htmlType="submit"
          >
            {" "}
            Responder{" "}
          </Button>
        </Form>


      </Modal>


    </>
  );
}
