// login.js

import React, { Component } from "react";
import {
  Icon,
  Form,
  Input,
  Button,
  Row,
  notification,
  Modal,
  Typography,
  Col
} from "antd";
import { Redirect } from "react-router-dom";
import axios from "axios";
import { login as saveData, getStatus, getToken } from "../../services/auth";

const logoProSolus = require("../../images/logoName.png");
const background = require("../../images/background.jpg");
const { Text } = Typography;

class NormalLoginForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nav: "",
      mainVisible: true,
      loginVisible: false,
      status: "", // -1: Funcionario Login;
      loading: false
    };
  }

  componentDidMount() {
    if (getToken()) {
      if (getStatus() === "1") this.setState({ nav: "/familiaProdutos" });
      else this.setState({ nav: "/home" });
    }
  }

  showMainModal = () => {
    this.setState({
      mainVisible: true,
      loginVisible: false
    });
    this.props.form.resetFields();
  };

  showModalLogin = status => {
    this.setState({
      loginVisible: true,
      mainVisible: false,
      status
    });
  };



  handleSubmitLogin = e => {
    e.preventDefault();
    this.setState({ loading: true });
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { login, senha } = values;

        if (this.state.status === 1) {

          axios
            .post("/funcionarios/login", { login, senha })
            .then(res => {
              saveData(
                res.data.token,
                res.data.user.status,
                res.data.user.name,
                res.data.user.id
              );
              notification.success({
                message: `Bem Vindo ao Sistema Pro Solus, ${res.data.user.name}`
              });
              this.setState({ nav: "/home" });
            })
            .catch(ex => {
              this.setState({ loading: false });
              if (ex.response.status === 401) {
                notification.error({
                  message: "ERRO",
                  description:
                    "O nome de usuário e senha não coincidem. Por favor, tente novamente."
                });
              } else {
                notification.error({
                  message: "ERRO",
                  description:
                    "Houve um problema aqui: Não sabemos ao certo, em caso de dúvidas, contate o suporte técnico."
                });
              }
            });
        } else {
          axios
            .post("/funcionarios/login/", {
              login,
              senha
            })
            .then(res => {
              saveData(
                res.data.token,
                res.data.user.status,
                res.data.user.name,
                res.data.user.id
              );

              notification.success({
                message: `Bem Vindo, ${res.data.user.name}`,
                description: "Seu login foi aprovado!"
              });

              this.setState({ nav: "/home" });
            })
            .catch(ex => {
              this.setState({ loading: false });

              if (ex.status === 401) {
                notification.error({
                  message: "Opa!",
                  description: "Houve um problema aqui: Credenciais Erradas."
                });
              } else {
                notification.error({
                  message: "Opa!",
                  description:
                    "Houve um problema aqui: Não sabemos ao certo, em caso de dúvidas, contate o suporte técnico."
                });
              }
            });
        }
      } else {
        this.setState({ loading: false });
      }
    });
  };

  render() {
    if (this.state.nav) {
      return <Redirect to={this.state.nav} />;
    }

    const { getFieldDecorator } = this.props.form;
    return (
      <Row>
        <img
          src={background}
          alt="Pro Solus"
        />
        <Modal
          style={{
            opacity: "0.90"
          }}
          mask={false}
          closable={false}
          footer={false}
          visible={this.state.mainVisible}
          centered
        >
          <Row>
            <Col
              span={10}
              pull={2}
              style={{ padding: 30, borderRight: "1px solid #E0E0DF" }}
            >
              <img
                style={{ height: 55, cursor: "auto", marginLeft: 5 }}
                src={logoProSolus}
                alt="Logo Pro Solus"
              />
            </Col>
            <Col span={12}>
              <Row style={{ marginTop: 35, textAlign: "center" }}>
                <Text
                  style={{
                    fontSize: "26px",
                    fontWeight: "bold",
                    fontFamily: "American Typewriter, serif",
                    // fontStyle: "oblique"
                  }}
                >Acesso ao sistema</Text>
              </Row>
            </Col>
          </Row>

          <Form onSubmit={this.handleSubmitLogin} style={{ marginTop: 10 }}>
            <Form.Item
              label="Login"
            >
              {getFieldDecorator("login", {
                rules: [
                  {
                    required: true,
                    message: "Por favor, insira seu nome de usuário"
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.50)" }} />
                  }
                  placeholder="Nome de usuário"
                />
              )}
            </Form.Item>

            <Form.Item
              label="Senha"
            >
              {getFieldDecorator("senha", {
                rules: [
                  { required: true, message: "Por favor, insira sua senha" }
                ]
              })(
                <Input.Password
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.50)" }} />
                  }
                  type="password"
                  placeholder="Senha"
                />
              )}
            </Form.Item>

            <Form.Item>
              <Button
                type="primary"
                block
                htmlType="submit"
                className="login-form-button"
                loading={this.state.loading}
                onClick={this.enterLoading}
                style={{
                  fontSize: "18px",
                  fontWeight: "bold"
                }}
              >
                Entrar
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      </Row>
    );
  }
}

const Login = Form.create({ name: "normal_login" })(NormalLoginForm);

export default Login;
