import { notification } from 'antd';

export const err503 = () => {
  notification.error({
    message: 'ERRO 503',
    description: 'Servidor está inacessível no momento, tente carregar a página novamente, se o erro persistir, entre em contato com o técnico.'
  });
}

export const err401 = () => {
  notification.error({
    message: 'ERRO 401',
    description: 'Erro ao realizar a ação. Você não tem permissão para isto.'
  });
}

export const errGeneral = () => {
  notification.error({
    message: 'ERRO',
    description: 'Erro ao realizar a ação. Tente carregar a página novamente, se o erro persistir, entre em contato com o técnico.'
  });
}

export const sucess = () => {
  notification.success({
    message: 'OPERAÇÃO REALIZADA COM SUCESSO!',
    // description: 'Ação realizada com sucesso.'
  });
}

export const successAdicionar = (m) => {
  notification.success({
    message: `${m} adicionado com sucesso`,
  })
}

export const waitService = () => {
  notification.warn({
    message: 'Aguarde um momento...'
  })
}