import React, { useState, useEffect } from "react";
import { Row, Icon, Input, Modal, Button, Divider, Typography, Table, Popover, Form } from "antd";
import axios from "axios";
import { err503, err401, errGeneral, sucess } from "../../services/messages";


const { Paragraph } = Typography;

const Marcas = props => {

    const { getFieldDecorator, } = props.form;
    const { confirm } = Modal;

    const [marca, setMarcas] = useState([]);
    const [marcasFiltered, setMarcasFiltered] = useState([]);

    const [visibleModalEditarMarca, setVisibleModalEditarMarca] = useState(false);
    const [visibleModalNovaMarca, setVisibleModalNovaMarca] = useState(false);

    const [state, setState] = useState(true);

    const [editarNome, setEditarNome] = useState([]);
    const [editarID, setEditarID] = useState('');

    useEffect(() => {
        axios.get("/marcas").then(res => {
            setMarcas(res.data);
            setMarcasFiltered(res.data);
            setState(true);
        })
            .catch(err => {
                if (err.response && err.response.status === 503) err503();
                else if (err.response && err.response.status === 401) err401();
                else errGeneral();
            });
    }, [state]);

    const filterMarcas = value => {
        setMarcasFiltered(
            marca.filter(r => (r.nome && r.nome.toLowerCase().includes(value.toLowerCase())))
        );
    };

    const excluirMarca = marca => {
        confirm({
            title: `Excluir a marca "${marca.nome}"?`,
            content: "Esta ação é definitiva, confirma a exclusão? ",
            okType: "danger",
            okText: "Excluir",
            cancelText: "Cancelar",
            onOk() {
                axios.delete(`/marcas/${marca._id}`).then(() => {
                    setState(false);
                    sucess();
                }).catch(err => {
                    if (err.response && err.response.status === 503) err503();
                    else if (err.response && err.response.status === 401) err401();
                    else errGeneral();
                });
            }
        });
    };

    const adicionarMarca = e => {
        e.preventDefault();
        props.form.validateFields((err, values) => {
            if (!err) {
                confirm({
                    title: `Adicionar a marca "${values.marca}"?`,
                    content: "Tem certeza que as informações estão corretas? ",
                    okType: "primary",
                    okText: "Adicionar",
                    cancelText: "Cancelar",
                    onOk() {
                        axios.post("/marcas/", { nome: values.marca }).then(() => {
                            setState(false);
                            props.form.setFields({
                                nome: {
                                    value: ""
                                },
                            });
                            sucess();
                            hideModalNovaMarca();
                        }).catch(err => {
                            if (err.response && err.response.status === 503) err503();
                            else if (err.response && err.response.status === 401) err401();
                            else errGeneral();
                        });
                    }
                });
            }
        });
    };

    const editarMarcas = e => {
        e.preventDefault();

        confirm({
            title: `Deseja mesmo editar essa marca?`,
            content: "Esta ação é definitiva, confirmar a edição?",
            okType: "danger",
            okText: "Confirmar",
            cancelText: "Cancelar",
            onOk() {
                axios.put("/marcas/", { nome: editarNome, id: editarID }).then(() => {
                    setState(false);
                    sucess();
                    hideModalEditarMarca();
                }).catch(err => {
                    if (err.response && err.response.status === 503) err503();
                    else if (err.response && err.response.status === 401) err401();
                    else errGeneral();
                });
            }
        });
    };

    const hideModalEditarMarca = () => {
        setEditarID('');
        setEditarNome('');
        setVisibleModalEditarMarca(false);
    };

    const hideModalNovaMarca = () => {
        setVisibleModalNovaMarca(false);
    };

    const showModalNovaMarca = () => {
        setVisibleModalNovaMarca(true);
    };

    const showModalEditarMarca = e => {
        setEditarID(e._id)
        setEditarNome(e.nome);
        setVisibleModalEditarMarca(true);
    };

    const columns = [
        {
            title: "Nome da marca",
            dataIndex: "nome",
            width: "20%",
            key: "nome",
            sorter: (a, b) => a.nome.localeCompare(b.nome)
        },
        {
            title: "Ação",
            dataIndex: "acao",
            width: "20%",
            key: "acao",
            render: (text, record) => (
                <span>
                    <Popover content="Editar">
                        <Icon
                            onClick={() => showModalEditarMarca(record)}
                            type="edit"
                            theme="twoTone"
                            twoToneColor="orange"
                        />
                    </Popover>

                    <Divider style={{ marginLeft: 15, marginRight: 15 }} type="vertical" />

                    <Popover content="Excluir">
                        <Icon
                            onClick={() => excluirMarca(record)}
                            type="delete"
                            theme="twoTone"
                            twoToneColor="#EB3B5A"
                        />
                    </Popover>
                </span>
            )
        }
    ];

    return (
        <>
            <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>Marcas</Paragraph>

            <Divider
                style={{
                    fontSize: 20,
                    minWidth: "60%",
                    width: "60%",
                    marginTop: 0,
                    marginLeft: "auto",
                    marginRight: "auto"
                }}>
                <Icon type="flag" />
            </Divider>

            <Row className="input-filter-responsive" style={{ display: "flex" }}>
                <h3>Pesquisar:&nbsp;&nbsp;</h3>

                <Input.Search
                    placeholder="Procurar por nome"
                    onChange={e => filterMarcas(e.target.value)}
                    style={{ marginRight: 15, marginBottom: 8 }}
                />

                <Button
                    onClick={() => showModalNovaMarca()}
                    type="primary"
                    icon="plus"
                    style={{ float: "right" }}
                >Nova Marca
                </Button>
            </Row>

            <Table
                style={{ marginTop: 20 }}
                rowKey="_id"
                columns={columns}
                dataSource={marcasFiltered}
            />

            <Modal
                visible={visibleModalNovaMarca}
                footer={null}
                onCancel={() => hideModalNovaMarca()}
                centered
            >
                <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
                    Cadastro de Marca
                </Paragraph>
                <Divider style={{
                    minWidth: "60%",
                    width: "60%",
                    marginTop: 0,
                    marginLeft: "auto",
                    marginRight: "auto"
                }}><Icon type="flag" />
                </Divider>

                <Form style={{ marginTop: 10 }} onSubmit={adicionarMarca}>
                    <Form.Item label='Nome da marca'>
                        {getFieldDecorator('marca', {
                            rules: [{ required: true, message: 'Por favor informe o nome da marca.' }],
                        })(
                            <Input prefix={<Icon type="bg-colors" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                placeholder="Nome da marca"
                            />,
                        )}
                    </Form.Item>
                    <Button
                        type="danger"
                        icon="check"
                        style={{ right: "-16%" }}
                        htmlType="reset"
                        onClick={() => hideModalNovaMarca()}
                    >
                        Cancelar{" "}
                    </Button>

                    <Button
                        type="primary"
                        icon="check"
                        style={{ right: "-36%" }}
                        htmlType="submit">Confirmar</Button>
                </Form>
            </Modal>

            <Modal
                visible={visibleModalEditarMarca}
                onCancel={() => hideModalEditarMarca()}
                centered
                footer={null}>
                <Paragraph style={{ fontSize: 22, textAlign: "center", marginBottom: 5 }}>
                    Editar Marca
                </Paragraph>

                <Divider style={{
                    minWidth: "40%",
                    width: "40%",
                    marginTop: 0,
                    marginLeft: "auto",
                    marginRight: "auto"
                }}>
                    <Icon type="flag" />
                </Divider>

                <Form style={{ marginTop: 10 }} onSubmit={editarMarcas}>
                    <Form.Item label="Nome da marca">
                        <Input prefix={<Icon type="bg-colors" style={{ color: "rgba(0,0,0,.25)" }} />}
                            placeholder="Nome da marca"
                            value={editarNome}
                            onChange={e => setEditarNome(e.target.value)} />
                    </Form.Item>

                    <Button
                        type="primary"
                        icon="check"
                        style={{ right: "-36%", display: "flex", marginTop: "45px" }}
                        htmlType="submit"
                    >Confirmar
                    </Button>
                </Form>
            </Modal>
        </>
    );
}

const WrappedNormalMarcaForm = Form.create({ name: "normal_marcas" })(Marcas);
const WrappedNormalMarcaFormEdit = Form.create({ name: "normal_Marcas_edit" })(
    Marcas
);

export default (WrappedNormalMarcaForm, WrappedNormalMarcaFormEdit);