import React, { useState, useEffect } from "react";
import {
  Row,
  List,
  Icon,
  Input,
  Modal,
  Form,
  Button,
  Divider,
  Popover,
  Typography,
  message,
  Upload,
  Select,
  Checkbox,
  Col,
  Spin
} from "antd";
import axios from "axios";
import { err503, err401, errGeneral, sucess } from "../../services/messages";
import "./listagemProdutos.css";

const { Paragraph, Title } = Typography;
const { confirm } = Modal;
const { Option } = Select;

const Produto = props => {
  const { getFieldDecorator } = props.form;

  //PRODUTOS
  const msgAddImage = "Adicionar Imagem";
  const msgEditImage = "Editar Imagem";
  const imgDefault = ["https://res.cloudinary.com/haken/raw/upload/v1583970117/xnnqsihzw19lzg4vtpci.png"];
  const [msgButtonImage, setMsgButtonImage] = useState(msgAddImage);
  const [loading, setLoading] = useState(false);
  const [visibleModalImege, setVisibleModalImage] = useState(false);
  const [produtos, setprodutos] = useState([]);
  const [produtosFiltered, setprodutosFiltered] = useState([]);
  //CADASTRO NOVO PRODUTO
  const [visibleModalNovoProduto, setvisibleModalNovoProduto] = useState(false);
  const [newFotoProduto, setnewFotoProduto] = useState([]);
  //EDIÇÃO PRODUTO
  const [visibleModalEditarProduto, setVisibleModalEditarProduto] = useState(false);
  const [editarCategoria, setEditarCategoria] = useState("")
  const [editarProdutoId, setEditarProdutoId] = useState("");
  const [habilityFilter, setHabilityFilter] = useState(true);
  const [categoria, setCategoria] = useState([]);
  const [values, setValues] = useState({
    name: "",
    descricao: "",
    faq: "",
    imagem: ""
  });
  const [state, setState] = useState(true);
  const [stateCategoria] = useState(true);

  useEffect(() => {
    axios
      .get("/produtos")
      .then(res => {
        setprodutos(res.data);
        console.log(res.data);
        setState(true);
        setprodutosFiltered(res.data);
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();
      });
  }, [state]);

  useEffect(() => {
    axios
      .get("/categorias")
      .then(res => setCategoria(res.data))
  }, [stateCategoria]);

  const filterprodutos = value => {
    setprodutosFiltered(
      produtos.filter(
        r =>
          (r.nome && r.nome.toLowerCase().includes(value.toLowerCase())) ||
          (r.categoria && r.categoria.toLowerCase().includes(value.toLowerCase())) ||
          (r.descricao &&
            r.descricao.toLowerCase().includes(value.toLowerCase()))
      )
    );
  };

  const editarProduto = e => {
    e.preventDefault();
    let produto = {};
    produto._id = editarProdutoId;
    produto.nome = values.name;
    produto.faq = values.faq;
    produto.imagem = values.imagem;
    produto.descricao = values.descricao;
    produto.categoria = editarCategoria;

    axios
      .put("/produtos/" + produto._id, produto)
      .then(() => {
        setState(false);
        hideModalEditarProduto();
        sucess();
      })
      .catch(err => {
        if (err.response && err.response.status === 503) err503();
        else if (err.response && err.response.status === 401) err401();
        else errGeneral();

      }).finally(() => {
        setState(false);
        props.form.setFields({
          nome: {
            value: ""
          },
          descricao: {
            value: ""
          },
          faq: {
            value: ""
          },
          id_categoria: {
            placeholder: ""
          },
          imagem: {
            value: ""
          }
        });
        hideModalNovoProduto();
      });

  };

  const excluirProduto = produtos => {
    confirm({
      title: `Excluir ${produtos.nome}`,
      content: "Esta ação é definitiva, confirma a exclusão?",
      okType: "danger",
      okText: "Excluir",
      cancelText: "Cancelar",
      onOk() {
        axios
          .delete(`/produtos/${produtos._id}`)
          .then(() => {
            setState(false);
            sucess();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          });
      }
    });
  };

  const criarnovoProduto = e => {
    e.preventDefault();
    props.form.validateFields((err, produto) => {
      if (!err) {

        produto.imagem = values.imagem;
        axios
          .post("/produtos", produto)
          .then(() => {

            sucess();
          })
          .catch(err => {
            if (err.response && err.response.status === 503) err503();
            else if (err.response && err.response.status === 401) err401();
            else errGeneral();
          }).finally(() => {
            setState(false);
            props.form.setFields({
              nome: {
                value: ""
              },
              descricao: {
                value: ""
              },
              faq: {
                value: ""
              },
              id_categoria: {
                placeholder: ""
              },
              imagem: {
                value: ""
              }
            });
            hideModalNovoProduto();
          })


      }
    });
  };

  const changeProjectPhoto = () => {
    setLoading(true);
    const formData = new FormData();
    formData.append("api_key", "784737987534174");
    formData.append("timestamp", Date.now() / 1000);
    formData.append("upload_preset", "hatsxc2p");
    formData.append("file", newFotoProduto[0]);

    axios
      .post("https://api.cloudinary.com/v1_1/haken/raw/upload", formData, {
        headers: { "X-Requested-With": "XMLHttpRequest" }

      })
      .then(res => {
        setMsgButtonImage(msgEditImage);
        setValues({ ...values, imagem: res.data.secure_url });
      })
      .catch(() => {
        setValues({ ...values, imagem: "" });
      }).finally(() => {
        setLoading(false);
      });
  };

  // função pra upload imagem
  const novoProdutoImagem = () => {
    setLoading(true);
    const upImagem = new FormData();
    upImagem.append("api_key", "784737987534174");
    upImagem.append("timestamp", Date.now() / 1000);
    upImagem.append("upload_preset", "hatsxc2p");
    upImagem.append("file", newFotoProduto[0]);

    axios
      .post("https://api.cloudinary.com/v1_1/haken/raw/upload", upImagem, {
        headers: { "X-Requested-With": "XMLHttpRequest" }

      })
      .then(res => {
        setMsgButtonImage(msgEditImage);
        setValues({ ...values, imagem: res.data.secure_url });
      })
      .catch((e) => {
        setValues({ ...values, imagem: "" });
      }).finally(() => {
        setLoading(false);
      });
  };
  //fim da função de upload

  //
  const beforeupImagem = file => {
    if (file.type === "image/jpeg" || file.type === "image/png") {
      const arquivos = [file];
      setnewFotoProduto(arquivos);

      return true;
    }

    message.error("Extensão Inválida! Permitido apenas imagens");
    setnewFotoProduto([]);
    return false;
  };

  const onChangeText = e => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const showModalNovoProduto = () => {
    setvisibleModalNovoProduto(true);
  };

  const hideModalNovoProduto = () => {
    setnewFotoProduto([]);
    setvisibleModalNovoProduto(false);
  };

  const showModalEditarProduto = item => {

    console.log(item.imagem)
    setValues({
      name: item.nome,
      descricao: item.descricao,
      faq: item.faq,
      imagem: item.imagem
    });
    setEditarCategoria(item.categoria);
    setEditarProdutoId(item._id);
    setVisibleModalEditarProduto(true);
  };

  const showModalImageProduto = item => {
    setValues({
      name: item.nome,
      descricao: item.descricao,
      faq: item.faq,
      imagem: item.imagem
    });
    setVisibleModalImage(true);
  }

  const hideModalImage = () => {
    setValues({
      name: "",
      descricao: "",
      faq: "",
      imagem: ""
    })
    setVisibleModalImage(false);
  }

  const hideModalEditarProduto = () => {
    setValues({
      name: "",
      descricao: "",
      faq: "",
      imagem: ""
    });
    setEditarCategoria("");
    setEditarProdutoId("");
    setEditarCategoria('');
    setnewFotoProduto([]);
    setVisibleModalEditarProduto(false);
  };

  return (
    <>
      <Paragraph style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}>
        {" "}
        Produtos{" "}
      </Paragraph>
      <Divider
        style={{
          fontSize: 20,
          minWidth: "60%",
          width: "60%",
          marginTop: 0,
          marginLeft: "auto",
          marginRight: "auto"
        }}
      >
        <Icon type="apartment" />
      </Divider>

      <Row className="input-filter-responsive" style={{ display: "flex" }}>
        <h3>Pesquisar:&nbsp;&nbsp;</h3>
        <Input.Search
          placeholder="Procurar por descrição ou nome"
          onChange={e => filterprodutos(e.target.value)}
          style={{ marginRight: 15, marginBottom: 8 }}
        />

        <Button
          onClick={() => {
            showModalNovoProduto();
            setMsgButtonImage(msgAddImage);
            setValues({ ...values, imagem: imgDefault });
          }
          }
          type="primary"
          icon="plus"
          style={{ float: "right" }}
        >
          {" "}
          Novo Produto{" "}
        </Button>
      </Row>

      <Row >
        <Checkbox
          onChange={({ target }) => {
            console.log(target.checked);
            setHabilityFilter(!target.checked);
            setState(false);
          }}

        >
          Filtrar :
        </Checkbox>


        <Select

          showSearch
          style={{ width: 600 }}
          placeholder="Filtrar por família de produtos"
          optionFilterProp="children"
          autoClearSearchValue="true"
          disabled={habilityFilter}
          // onChange={onChange}
          // onFocus={onFocus}
          // onBlur={onBlur}
          // onSearch={onSearch}
          onSelect={filterprodutos}

          filterOption={(input, option) =>
            option.props.children
              .toLowerCase()
              .indexOf(input.toLowerCase()) >= 0
          }
        >
          {categoria.map((valor, index) => (
            <Option value={valor._id} key={index}>
              {valor.nome}
            </Option>
          ))}
        </Select>
      </Row>

      {/* 1° modal   */}
      <Modal
        visible={visibleModalNovoProduto}
        footer={null}
        onCancel={() => hideModalNovoProduto()}
        centered
        destroyOnClose={true}
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Cadastro de Produto{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        ></Divider>

        <Form style={{ marginTop: 10 }} onSubmit={criarnovoProduto}>
          <Form.Item label="Nome do Produto">
            {getFieldDecorator("nome", {
              rules: [
                {
                  required: true,
                  message: "Por favor informe o nome do Produto."
                }
              ]
            })(
              <Input
                prefix={
                  <Icon type="font-size" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder="Nome do Produto"
              />
            )}
          </Form.Item>

          <Form.Item label="Descrição">
            {getFieldDecorator("descricao", {
              rules: [
                {
                  required: true,
                  message: "Por favor informe a descrição do Produto."
                }
              ]
            })(<Input.TextArea placeholder="Descrição do Produto" />)}
          </Form.Item>

          <Form.Item label="FAQ">
            {getFieldDecorator("faq", {
              rules: [
                {
                  required: true,
                  message: "Por favor informe o FAQ do Produto."
                }
              ]
            })(
              <Input.TextArea
                prefix={
                  <Icon type="form" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder="FAQ do Produto"
              />
            )}
          </Form.Item>

          <Form.Item label="Família do Produto">
            {getFieldDecorator("id_categoria", {
              rules: [
                {
                  required: true,
                  message: "Por favor selecione a familia do produto"
                }
              ]
            })(
              <Select
                mode="unique"
                showSearch
                placeholder="Selecione a família do produto"
                optionFilterProp="dropdownRender"
                filterOption={(input, option) =>
                  option.props.children
                    .toLowerCase()
                    .indexOf(input.toLowerCase()) >= 0
                }
              >
                {categoria.map((valor, index) => (
                  <Option value={valor._id} key={index}>
                    {valor.nome}
                  </Option>
                ))}
              </Select>
            )}
          </Form.Item>


          <Form.Item >
            <Row type="flex" justify="center" >
              <Spin spinning={loading} tip="Aguarde..." size="large">
                <Row type="flex" justify="center" style={{ marginBottom: "12px" }}>
                  <img alt="Imagem do produto" style={{ maxWidth: 230, maxHeight: 185, minWidth:230, minHeight:185 }} src={values.imagem}/>
                </Row>
              </Spin>
            </Row>

            <Row>
              <Col span={12} style={{ textAlign: 'center' }}>
                <Upload
                  showUploadList={false}
                  beforeUpload={beforeupImagem}
                  customRequest={novoProdutoImagem}
                  fileList={newFotoProduto}
                  acecept="image/*"
                >
                  <Button disabled={loading} shape="round" type="primary" size="small">{msgButtonImage}</Button>
                </Upload>
              </Col>
              <Col span={12} style={{ textAlign: 'center' }}>

                <Button
                  disabled={loading}
                  shape="round"
                  type="danger"
                  icon="delete"
                  size="small"
                  onClick={() => {
                    setMsgButtonImage(msgAddImage);
                    setValues({ ...values, imagem: imgDefault });
                    console.log(values.imagem);
                  }}

                >Remover Imagem </Button>
              </Col>
            </Row>

          </Form.Item>

          <div style={{ height: "15px" }}></div>

          <Button
            disabled={loading}
            type="primary"
            icon="check"
            htmlType="submit"
            block
            shape="round">
            {" "}
            Confirmar{" "}
          </Button>
        </Form>
      </Modal>

      <Modal
        visible={visibleModalEditarProduto}
        footer={null}
        onCancel={hideModalEditarProduto}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}
          Editar Produto{" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="file" />
        </Divider>
        <Form style={{ marginTop: 10 }} onSubmit={editarProduto}>
          <Form.Item label='Nome do Produto'>
            <Input
              prefix={
                <Icon type="font-size" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              value={values.name} //edição "_id"
              name="name"
              type="text"
              onChange={onChangeText}
            />
          </Form.Item>
          <Form.Item label='Descrição'>
            <Input.TextArea
              rows={5}
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              name="descricao"
              value={values.descricao}
              onChange={onChangeText}
            />
          </Form.Item>
          <Form.Item label='FAQ'>
            <Input.TextArea
              rows={5}
              prefix={
                <Icon type="file-text" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              name="faq"
              value={values.faq}
              onChange={onChangeText}
            />

          </Form.Item>

          <Form.Item label='Família do Produto'>
            <Select
              name="categoria"
              mode="unique"
              showSearch
              placeholder="Selecione a família do produto"
              optionFilterProp="dropdownRender"
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
              value={editarCategoria}
              onChange={e => setEditarCategoria(e)}
            >
              {categoria.map((valor, index) => (
                <Option value={valor._id} key={index}>
                  {valor.nome}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item>
            <Row type="flex" justify="center" >
              <Spin spinning={loading} tip="Aguarde..." size="large">

                <Row type="flex" justify="center" style={{ marginBottom: "12px" }}>
                  <img alt="Imagem do produto" style={{ maxWidth: 230, maxHeight: 185, minWidth:230, minHeight:185 }} src={values.imagem}/>
                </Row>

              </Spin>
            </Row>

            <Row >
              <Col span={12} style={{ textAlign: 'center' }}>
                <Upload
                  showUploadList={false}
                  beforeUpload={beforeupImagem}
                  customRequest={changeProjectPhoto}
                  fileList={newFotoProduto}
                  acecept="image/*"
                >

                  <Button disabled={loading} shape="round" type="primary" size="small" >{msgButtonImage}</Button>
                </Upload>
              </Col>

              <Col span={12} style={{ textAlign: 'center' }}>

                <Button
                  disabled={loading}
                  shape="round"
                  type="danger"
                  icon="delete"
                  size="small"
                  onClick={() => {
                    setMsgButtonImage(msgAddImage);
                    setValues({ ...values, imagem: imgDefault });
                    console.log(values.imagem);
                  }}

                >Remover Imagem </Button>
              </Col>

            </Row>
          </Form.Item>


          <Row type="flex" justify="center" style={{ marginTop: "60px" }}>

            <Button
              disabled={loading}
              type="primary"
              icon="check"
              htmlType="submit"
              block
              shape="round">
              {" "}
              Confirmar{" "}
            </Button>
          </Row>
        </Form>
      </Modal>



      <Modal
        visible={visibleModalImege}
        footer={null}
        onCancel={hideModalImage}
        centered
      >
        <Paragraph
          style={{ fontSize: 30, textAlign: "center", marginBottom: 5 }}
        >
          {" "}{values.name}
          {" "}
        </Paragraph>
        <Divider
          style={{
            minWidth: "60%",
            width: "60%",
            marginTop: 0,
            marginLeft: "auto",
            marginRight: "auto"
          }}
        >
          <Icon type="picture" />
        </Divider>

        <Form.Item>
          <Row type="flex" justify="center" >
            <Spin spinning={loading} tip="Aguarde..." size="large">
              <Row type="flex" justify="center" style={{ marginBottom: "12px" }}>
                <img alt="Imagem do produto" style={{ maxWidth: 500, maxHeight: 350, minWidth: 500, minHeight:350 }} src={values.imagem}/>
              </Row>
            </Spin>
          </Row>
        </Form.Item>
      </Modal>

      <List
        itemLayout="vertical"
        size="large"
        style={{ marginTop: 20 }}
        pagination={{ pageSize: 3 }}
        dataSource={produtosFiltered}
        renderItem={(item, text, record) => (
          <List.Item
            key={item._id}
            actions={[
              <Popover content="Editar">
                <Icon
                  onClick={() => {
                      showModalEditarProduto(item)
                      if(values.imagem === imgDefault){
                        setMsgButtonImage(msgAddImage);
                      }else {
                        setMsgButtonImage(msgEditImage);
                      }
                    }
                  }
                  style={{ fontSize: '18px'}}
                  type="edit"
                  theme="twoTone"
                  twoToneColor="orange"
                />
              </Popover>,

              <Popover content="Exclusão">
                <Icon
                  style={{ fontSize: '18px'}}
                  onClick={() => excluirProduto(item)}
                  type="delete"
                  theme="twoTone"
                  twoToneColor="#EB3B5A"
                />
              </Popover>
            ]}
            extra={
              <span style={{cursor:"pointer"}} onClick={()=>{
                showModalImageProduto(item);
              }}>
                <img alt="Imagem do produto"
                  style={{ maxWidth: 130, maxHeight: 110, minWidth:130, minHeight:110 }}
                  src={item.imagem}
                />
              </span>
            }
          >
            <Typography>
              <Row style={{ display: "grid" }}>
                <Title level={3} style={{ marginRight: 14 }}>
                  {" "}
                  {item.nome}{" "}
                </Title>
              </Row>
              <Paragraph
                type="secondary"
                ellipsis={{ rows: 3, expandable: true }}
              >
                {" "}
                {item.descricao}{" "}
              </Paragraph>
            </Typography>
          </List.Item>
        )}
      />
    </>
  );
};
const WrappedNormalProdutoForm = Form.create({
  name: "normal_produto"
})(Produto);
const WrappedNormalProdutoFormEdit = Form.create({
  name: "normal_produto_edit"
})(Produto);
export default (WrappedNormalProdutoForm, WrappedNormalProdutoFormEdit);