export const TOKEN_KEY = 'solus_token';
export const STATUS = 'solus_status';
export const NOME = 'solus_nome';
export const ID = 'solus_id';

export const getToken = () => localStorage.getItem(TOKEN_KEY);
export const getStatus = () => localStorage.getItem(STATUS);
export const getNome = () => localStorage.getItem(NOME);
export const getID = () => localStorage.getItem(ID);

export const login = (token, status, nome, id) => {
  localStorage.setItem(TOKEN_KEY, token);
  localStorage.setItem(STATUS, status);
  localStorage.setItem(NOME, nome);
  localStorage.setItem(ID, id);
};

export const logout = () => {
  localStorage.removeItem(TOKEN_KEY);
  localStorage.removeItem(STATUS);
  localStorage.removeItem(NOME);
  localStorage.removeItem(ID);
};