import React, { useState } from "react";
import { Link } from "react-router-dom";
import { getStatus } from "../../services/auth";
import { Layout, Menu, Icon } from "antd";
import "./menu.css";

const { Sider } = Layout;
const { SubMenu } = Menu;

export default function MainMenu() {
  const [collapsed, setCollapsed] = useState(false);
  const onCollapse = collapsed => {
    setCollapsed(collapsed);
  };
  const logo = require("../../images/logo.png");
  const logoName = require("../../images/logoName3.png");

  return (
    // tinha isso className="espaco"
    <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>

      {/* preciso inserir uma condicional aqui pra ver como vai ficar a logo */}
      <div className="logo-pro-solus" >
        {
          (collapsed)
            ? <img className="pro-logo" src={logo} alt="Logo Pro" />
            : <img className="pro-logo-name" src={logoName} alt="Logo Pro" />
        }

      </div>
      <Menu theme="dark" defaultSelectedKeys={["1"]} mode="inline">
        <Menu.Item key="1">
          <Link to={"/layout"}>
            <Icon type="pie-chart" />
            <span>Home</span>
          </Link>
        </Menu.Item>

        <SubMenu
          key="sub1"
          title={
            <span>
              <Icon type="tool" />
              <span>Insumos</span>
            </span>
          }
        >
          <Menu.Item key="2">
            <Link to={"/bicos"}>Bicos</Link>
          </Menu.Item>
          <Menu.Item key="4">
            <Link to={"/listagemProdutos"}>Produtos</Link>
          </Menu.Item>
          <Menu.Item key="3">
            <Link to={"/familiaProdutos"}>Família de Produtos</Link>
          </Menu.Item>
          <Menu.Item key="10">
            <Link to={"/marcas"}>Marcas</Link>
          </Menu.Item>
        </SubMenu>

        <Menu.Item key="5">
          <Link to={"/chamados"}>
            <Icon type="desktop" />
            <span>Chamados</span>
          </Link>
        </Menu.Item>

        <SubMenu
          key="sub2"
          title={
            <span>
              <Icon type="team" />
              <span>Equipe</span>
            </span>
          }
        >
          {getStatus() === 'false' ? ''
            : <Menu.Item key="6">
              <Link to={"/funcionario"}>Membros</Link>
            </Menu.Item>}
          <Menu.Item key="7">
            <Link to={"/usuarios"}>Clientes</Link>
          </Menu.Item>
        </SubMenu>

        <Menu.Item key="9">
          <Link to={"/orcamentos"}>
            <Icon type="folder-open" />
            <span>Orçamentos</span>
          </Link>
        </Menu.Item>

        <Menu.Item>
          <Link to={"/estatisticas"}>
            <Icon type="bar-chart" />
            <span>Estatísticas</span>
          </Link>
        </Menu.Item>

      </Menu>
    </Sider>
  );
}
